import React from 'react';
import PropTypes from 'prop-types';

import pic01 from '../images/pic01.jpg';
import pic02 from '../images/pic02.jpg';
import pic03 from '../images/pic03.jpg';

class Blog extends React.Component {
  componentDidMount() {
    this.props.onPageIn();
  }

  componentWillUnmount() {
    this.props.onPageOut();
  }

  render() {

    let close = <div className="close" onClick={() => this.props.navigateWithTimeout('page', '/')}></div>;
    let blogDataArr = this.props.blogData;

    return (
      <article id="blog" className={`active ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
        <h2 className="major">Blog</h2>
        <span className="image main"><img src={pic03} alt="" /></span>
        <ul className="alt">
          <a href="javascript:;" onClick={() => this.props.navigateWithTimeout('page', '/postPage')}>Read the post...</a>
          {blogDataArr.map(({node}) => (
            <li key={node.id}>
              <header>
                <h3>{node.frontmatter.title}</h3>
                <p className="date">{node.frontmatter.date} - {node.timeToRead} minute read</p>
              </header>
              <p>{node.excerpt}</p>
            </li>
          ))}
        </ul>
        {close}
      </article>
    )
  }
}

Blog.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool
}

export default Blog;


